  .list {
      display: block;
      background-color: rgba(255,255,255,.8);
      box-shadow: 0 1px 3px rgba(0,0,0,.12),0 1px 2px rgba(0,0,0,.24);
      border-radius: 2px;
      -webkit-transition: all .2s ease-in-out;
      transition: all .2s ease-in-out;
  }
  
  .list-header {
      display: block;
      float: left;
      width: 35%;
      overflow: hidden;
      position: relative;
      border-radius: 2px 0 0 2px;
  }
  
  .list-header img {
      width: 100%;
  }
  
  .list-header .list-header-image:after {
      display: block;
      content: '';
      background-color: rgba(0,0,0,.4);
      position: absolute;
      top: 100%;
      left: 100%;
      width: 100px;
      height: 100px;
      box-shadow: 0 0 200px 100px rgba(0,0,0,.58);
      z-index: 1;
      -webkit-transform: rotate(45deg);
      transform: rotate(45deg);
      opacity: 0;
      filter: alpha(opacity=0);
      transition: all .8s;
      -moz-transition: all .8s;
      -webkit-transition: all .8s;
      -o-transition: all .8s;
  }
  
  .list-content {
      display: block;
      margin-left: 35%;
      padding: 15px;
      position: relative;
  }
  
  .list-meta {
      margin-bottom: 16px;
      font-size: 13px;
      line-height: 100%;
      color: #9A9A9A;

  }
  
  .list-meta .list-meta-item {
      display: inline-block;
      margin-right: 18px;
      color: #9A9A9A;
  }
  
  .list:after {
      display: block;
      clear: both;
      content: '';
  }
  
  .list:hover {
      box-shadow: 0 10px 20px rgba(0,0,0,.19),0 6px 6px rgba(0,0,0,.23);
  }
  
  
  
  @media only screen and (max-width: 991px) {
    .list-header {
      display: block;
      width: 100%;
      padding: 0;
      border-radius: 0;
    }
    
    .list-content {
      display: block;
      width: 100%;
      margin-left: 0;
      padding: 15px;
      clear: both;
    }
  }
